
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
.home {
  position: relative;
  background-color: #ededed;
  margin-bottom: 69px;
}
.home .swiper-container {
    position: relative;
    height: 210px;
}
.home .swiper-container .swiper-wrapper {
      height: 100%;
}
.home .swiper-container .swiper-wrapper .swiper-slide {
        display: inline-block;
        height: 100%;
}
.home .swiper-container .swiper-wrapper .swiper-slide a {
          display: inline-block;
          width: 100%;
          height: 100%;
}
.home .swiper-container .swiper-wrapper .swiper-slide a img {
            display: inline-block;
            width: 100%;
            height: 100%;
}
.home .swiper-container .swiper-pagination {
      opacity: 1;
      display: block;
      width: 300px;
      height: 20px;
      position: absolute;
      bottom: 5px;
      left: 50%;
      margin-left: -150px;
      text-align: center;
}
.home .swiper-container .swiper-pagination .swiper-pagination-bullet {
        width: 30px;
        height: 5px;
        display: inline-block;
        background: #E8ECF1;
        opacity: .4;
        border-radius: 3px;
}
.home .swiper-container .swiper-pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
          opacity: 1;
          background: #fff;
}
.home .zhubo {
    height: 90px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background-color: white;
}
.home .zhubo > a {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      display: inline-block;
      text-align: center;
}
.home .zhubo > a img {
        width: 60px;
        height: 60px;
        margin: 5px auto;
        border-radius: 30px;
}
.home .zhubo > a span {
        display: block;
        font-size: 12px;
}
.home .youhuiquan {
    height: 90px;
    background-color: white;
    margin: 10px 0;
    border: solid 1px white;
}
.home .youhuiquan a {
      display: block;
      height: 100%;
      width: 100%;
      text-align: center;
}
.home .youhuiquan a img {
        width: 92%;
        height: 82px;
        margin: 4px auto;
}
.home > ul {
    margin-top: 10px;
    background-color: white;
}
.home > ul li {
      margin-bottom: 10px;
}



