
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
.tabBar {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 49px;
  line-height: 49px;
  border-top: solid 1px #ededed;
  background-color: white;
}
.tabBar > a {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
    display: block;
    font-size: 14px;
    color: #4d555d;
    height: 100%;
}
.tabBar > a.router-link-active {
      color: #333;
}
.tabBar > a.router-link-active .tab-item .u-icon-tabBar-index {
        background-image: url("images/home_click.png");
}
.tabBar > a.router-link-active .tab-item .u-icon-tabBar-brand {
        background-image: url("images/brand_selected.png");
}
.tabBar > a.router-link-active .tab-item .u-icon-tabBar-live {
        background-image: url("images/liveselected.png");
}
.tabBar > a.router-link-active .tab-item .u-icon-tabBar-shopcart {
        background-image: url("images/shopping_card_selected.png");
}
.tabBar > a.router-link-active .tab-item .u-icon-tabBar-my {
        background-image: url("images/my_selected.png");
}
.tabBar > a .tab-item {
      text-align: center;
      position: relative;
      height: 100%;
}
.tabBar > a .tab-item .common_i {
        display: block;
        position: relative;
        text-align: center;
        width: 32px;
        height: 32px;
        margin: 0 auto;
        background-size: 32px 32px;
        background-repeat: no-repeat;
}
.tabBar > a .tab-item .common_i .badge {
          text-align: center;
          position: absolute;
          display: block;
          width: 16px;
          height: 16px;
          line-height: 16px;
          background-color: red;
          color: white;
          font-size: 8px;
          top: 0;
          right: -5px;
          border-radius: 8px;
}
.tabBar > a .tab-item .common_i .badge.bigNum {
            font-size: 8px;
}
.tabBar > a .tab-item .u-icon-tabBar-index {
        background-image: url("images/home.png");
}
.tabBar > a .tab-item .u-icon-tabBar-brand {
        background-image: url("images/brand.png");
}
.tabBar > a .tab-item .u-icon-tabBar-live {
        background-image: url("images/live.png");
}
.tabBar > a .tab-item .u-icon-tabBar-shopcart {
        background-image: url("images/shopping.png");
}
.tabBar > a .tab-item .u-icon-tabBar-my {
        background-image: url("images/my.png");
}
.tabBar > a .tab-item > span {
        position: absolute;
        bottom: 3px;
        left: 0;
        width: 100%;
        height: 12px;
        line-height: 12px;
        text-align: center;
        font-size: 10px;
}



