
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
.shopcart {
  position: relative;
  background-color: #ededed;
}
.shopcart .nav-head {
    position: fixed;
    height: 50px;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    background-color: white;
    font-size: 20px;
    line-height: 50px;
    position: relative;
}
.shopcart .nav-head:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      border-top: 1px solid rgba(7, 17, 27, 0.1);
}



