
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
.cell {
  background-color: white;
}
.cell .section_header {
    height: 30px;
    padding: 5px 15px;
}
.cell .section_header i {
      display: block;
      float: left;
      height: 20px;
      width: 15px;
      margin: 5px 0;
      background-image: url("images/tuijian.png");
      background-size: 15px 20px;
      background-repeat: no-repeat;
}
.cell .section_header span {
      float: left;
      margin-left: 5px;
      font-size: 15px;
      color: darkgray;
      vertical-align: top;
      height: 100%;
      line-height: 30px;
}
.cell .row_header {
    height: 60px;
    position: relative;
}
.cell .row_header img {
      float: left;
      height: 50px;
      width: 50px;
      margin: 5px 10px;
}
.cell .row_header .title {
      color: #333333;
      font-size: 18px;
      padding: 5px 0;
      position: absolute;
      top: 5px;
      left: 65px;
}
.cell .row_header .subtitle {
      position: absolute;
      bottom: 10px;
      left: 65px;
      font-size: 12px;
      color: darkgray;
}
.cell .row_header .positionWrap {
      padding-top: 6px;
}
.cell .row_header .positionWrap .position_icon {
        float: right;
        display: block;
        height: 17px;
        width: 12px;
        background-image: url("images/location.png");
        background-size: 12px 17px;
        background-repeat: no-repeat;
}
.cell .row_header .positionWrap .position {
        float: right;
        color: darkgray;
        font-size: 13px;
        vertical-align: top;
        margin-right: 10px;
        line-height: 17px;
}
.cell .row_header .date {
      position: absolute;
      font-size: 12px;
      color: darkgray;
      bottom: 15px;
      right: 10px;
}
.cell .content {
    position: relative;
    height: 210px;
}
.cell .content img {
      width: 100%;
      height: 100%;
}
.cell .content a:first-of-type {
      display: block;
      height: 70px;
      width: 70px;
      background-image: url("images/Playkey.png");
      background-size: 70px 70px;
      background-repeat: no-repeat;
      position: absolute;
      top: 50%;
      margin-top: -35px;
      left: 50%;
      margin-left: -35px;
}
.cell .content a:last-of-type {
      display: block;
      height: 15px;
      width: 42px;
      background-image: url("images/live_tag.png");
      background-size: 42px 15px;
      background-repeat: no-repeat;
      position: absolute;
      top: 15px;
      right: 15px;
}
.cell .content span {
      color: white;
      font-size: 18px;
      position: absolute;
      bottom: 10px;
      left: 15px;
}



