
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
.cell2 {
  background-color: white;
}
.cell2 .section_header {
    height: 30px;
    padding: 5px 15px;
}
.cell2 .section_header i {
      display: block;
      float: left;
      height: 13px;
      width: 15px;
      margin: 8px 0;
      background-image: url("images/Featured.png");
      background-size: 15px 13px;
      background-repeat: no-repeat;
}
.cell2 .section_header span {
      float: left;
      margin-left: 5px;
      font-size: 15px;
      color: #333;
      vertical-align: top;
      height: 100%;
      line-height: 30px;
}
.cell2 .content {
    display: block;
    height: 125px;
}
.cell2 .content > img {
      display: inline-block;
      vertical-align: top;
      width: 45%;
      height: 125px;
}
.cell2 .content .right_wrap {
      display: inline-block;
      position: relative;
      width: 50%;
      height: 100%;
}
.cell2 .content .right_wrap p {
        color: #333;
        margin: 5px 5px 30px 0;
}
.cell2 .content .right_wrap > span {
        position: absolute;
        bottom: 40px;
        left: 0;
        text-align: center;
        display: block;
        width: 55px;
        height: 18px;
        border: solid 1px darkgray;
        border-radius: 5px;
        line-height: 20px;
        font-size: 12px;
}
.cell2 .content .right_wrap div {
        margin-top: 10px;
        height: 26px;
        position: absolute;
        bottom: 5px;
        left: 0;
}
.cell2 .content .right_wrap div img {
          width: 26px;
          border-radius: 13px;
          display: inline-block;
          vertical-align: top;
}
.cell2 .content .right_wrap div span {
          display: inline-block;
          font-size: 12px;
          height: 26px;
          line-height: 26px;
          margin-left: 5px;
}



