
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
.showpic {
  position: relative;
  background-color: #ededed;
}
.showpic .nav-head {
    height: 50px;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    font-size: 20px;
    line-height: 50px;
    position: relative;
    position: fixed;
    z-index: 100;
    background-color: white;
}
.showpic .nav-head:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.showpic img {
    margin-top: 50px;
    width: 100%;
    margin-bottom: 0;
}



