
@charset "UTF-8";
/*1px像素边框*/
/*2倍和3倍图片*/
body, html {
  line-height: 1;
  font-weight: 200;
  font-family: "PingFang SC", "Helvetica Neue", Helvetica, STHeiTi, Arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
    content: '';
    display: block;
    height: 0;
    line-height: 0;
    clear: both;
    visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px:after {
    -webkit-transform: scaleY(0.7);
    transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px:after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.my {
  position: relative;
  background-color: #ededed;
}
.my .nav-head {
    height: 50px;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    background-color: white;
    font-size: 20px;
    line-height: 50px;
    position: relative;
}
.my .nav-head:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.my .section0 {
    height: 80px;
    background-color: white;
}
.my .section0 a div:first-of-type {
      width: 19%;
      display: inline-block;
      float: left;
}
.my .section0 a div:first-of-type img {
        width: 50px;
        border-radius: 25px;
        margin: 15px 10px;
}
.my .section0 a div:last-of-type {
      position: relative;
      display: inline-block;
      float: left;
      width: 80%;
      height: 100%;
      font-size: 14px;
}
.my .section0 a div:last-of-type p:first-of-type {
        margin-top: 20px;
}
.my .section0 a div:last-of-type p:last-of-type {
        margin-top: 10px;
}
.my .section0 a div:last-of-type i {
        display: block;
        width: 7px;
        height: 12px;
        float: right;
        background-image: url("images/more.png");
        background-size: 7px 12px;
        background-repeat: no-repeat;
        position: absolute;
        right: 10px;
        top: 50%;
        margin-top: -6px;
}
.my .section1 {
    margin-top: 10px;
    height: 100px;
    border: solid 1px white;
    background-color: white;
}
.my .section1 div:first-of-type {
      margin: 10px 10px;
}
.my .section1 div:first-of-type span:first-of-type {
        color: #333333;
        font-size: 13px;
        line-height: 100%;
}
.my .section1 div:first-of-type i {
        display: block;
        width: 7px;
        height: 12px;
        float: right;
        background-image: url("images/more.png");
        background-size: 7px 12px;
        background-repeat: no-repeat;
        margin-left: 5px;
}
.my .section1 div:first-of-type span:last-of-type {
        color: darkgrey;
        font-size: 13px;
        float: right;
        line-height: 100%;
}
.my .section1 div:last-of-type {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.my .section1 div:last-of-type a {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        display: inline-block;
        text-align: center;
        margin-top: 10px;
}
.my .section1 div:last-of-type a img {
          width: 35px;
          height: 28px;
}
.my .section1 div:last-of-type a span {
          display: block;
          font-size: 12px;
          margin-top: 5px;
}
.my .section2 {
    margin-top: 10px;
    background-color: white;
    font-size: 14px;
}
.my .section2 ul li {
      position: relative;
}
.my .section2 ul li:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        display: block;
        width: 100%;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.my .section2 ul li div {
        width: 99%;
        height: 44px;
}
.my .section2 ul li div span {
          float: left;
          display: inline-block;
          margin: 0 10px;
          height: 44px;
          line-height: 44px;
}
.my .section2 ul li div .tel {
          float: right;
}
.my .section2 ul li div i {
          display: block;
          width: 7px;
          height: 12px;
          float: right;
          background-image: url("images/more.png");
          background-size: 7px 12px;
          background-repeat: no-repeat;
          margin: 16px 10px;
}



